.section {
  background-color: white;
  margin: 10px 0;
}
.section__header {
  padding: 20px;
  display: grid;
  grid-template-columns: 1fr auto;
  border-bottom: 1px solid #ddd;
}

@media (max-width: 550px) {
  .section__header {
    grid-template-columns: 1fr;
    grid-gap: 20px;
    font-size: 15px;
  }
}

.section__title {
  margin: 0;
}

.section__body {
  padding: 20px;
}
