.form {
  margin: 1px 0 8px;
  padding: 15px;
  display: grid;
  grid-template-columns: 5fr 1fr;
}

@media (max-width: 550px) {
  .form {
    grid-template-columns: 1fr;
  }
}

.form__textInput {
  opacity: 0.5;
  padding: 4px 5px;
}

.form__button {
  margin-left: 10px;
  padding: 8px 3px;
  background-color: rgb(2, 127, 142);
  border: 1px solid rgb(2, 127, 142);
  color: rgb(255, 255, 255);
  font-size: 14px;
  transition: 0.5s;
  cursor: pointer;
}

@media (max-width: 550px) {
  .form__button {
    margin: 0 auto;
    width: 100%;
    margin-top: 10px;
  }
}

.form__button:hover {
  background-color: rgba(2, 127, 142, 0.8);
  border-color: rgba(2, 127, 142, 0.8);
  transform: scale(1.02);
}
